input[type=checkbox] {
  vertical-align: top;
  width: 17px;
  height: 17px;
  margin: 0 3px 0 0;
}
input[type=checkbox] + label {
  cursor: pointer;
  font-size: 17px;
  line-height: 30px;
}
input[type=checkbox]:not(checked) {
  position: absolute;
  opacity: 0;
}
input[type=checkbox]:not(checked) + label {
  position: relative;
  padding: 0 0 0 35px;
}
input[type=checkbox]:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  height: 22px;
  border: 1px solid #CDD1DA;
  background: #FFF;
}
input[type=checkbox]:not(checked) + label:after {
  font-family: 'FontAwesome';
  font-size: 17px;
  content: '\f00c';
  color: #16a085;
  position: absolute;
  left: 4px;
  opacity: 0;
  transition: all .2s;
}
input[type=checkbox]:checked + label:after {
  opacity: 1;
}
